import { request } from 'utils';

const prefix = '/api/students';

export const getEnrollSolutions = ({ page, pageSize = 10, orderBy, isPagingEnabled = true } = {}) =>
  request({
    prefix,
    url: `/EnrollSolution`,
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled },
    data: {},
  });

export const createEnrollSolution = ({
  id,
  label,
  courseId,
  minTotalPoint,
  minPointPerSubject,
  noOfStudent,
  reviewTypeId,
} = {}) =>
  request({
    prefix,
    url: `/EnrollSolution`,
    method: 'POST',
    params: {},
    data: { id, label, courseId, minTotalPoint, minPointPerSubject, noOfStudent, reviewTypeId },
  });

export const getEnrollSolutionById = ({ id } = {}) =>
  request({
    prefix,
    url: `/EnrollSolution/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const updateEnrollSolution = ({
  id,
  label,
  courseId,
  minTotalPoint,
  minPointPerSubject,
  noOfStudent,
  reviewTypeId,
} = {}) =>
  request({
    prefix,
    url: `/EnrollSolution/${id}`,
    method: 'PUT',
    params: {},
    data: { id, label, courseId, minTotalPoint, minPointPerSubject, noOfStudent, reviewTypeId },
  });

export const deleteEnrollSolution = ({ id } = {}) =>
  request({
    prefix,
    url: `/EnrollSolution/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



